    getCurrency(amount) {
        if (amount) {
            let final = this.currency.transform(amount, '', 'symbol-narrow', '1.2-2');
            return final.split('$').join('');
        }
    }

    initAmounts(): FormGroup {
        // returning object of form group
        return this._FB.group({
            amount: ['', Validators.required],
            giving_type: ['', Validators.required]
        });
    }

    addNewInputField(index): void {
        this.amounts = this.form.get('amounts') as FormArray;
        this.amounts.push(this.initAmounts());
    }

    removeInputField(i: number): void {
        this.cover_fee = false;

        const control = <FormArray>this.form.controls.amounts;

        // subtracting amount from total
        if (this.amount) {
            this.amount = this.amount - +control.value[i].amount;
        }
        // removing index
        control.removeAt(i);

        // setting to button
        this.button_currency = this.getCurrency(this.amount);
        this.pledge_amount = +this.button_currency - +this.cover_fee_amount;
        this.pledge_amount = this.pledge_amount.toFixed(2);

    }

