    async logOutUser() {
        this.loader.presentLoading().then(() => {
            this.Auth.logoutUser();
            this.event_provider.userlogout();               // calling event
            this.event_provider.refresh_home();               // calling event
            this.logoService.setStatus(false);
            let login_url = '/login';
            this.router.navigate([login_url]);
            this.loader.stopLoading();
        });
    }

    deleteSource(i) {
        const body = JSON.stringify({
            auth: 'Bearer ' + this.auth.access_token,
            // source_id: 1867
            source_id: this.wallet_id,
            church_id: this.ch_id
        });

        this.loader.presentLoading().then(() => {
            // delete user payment source
            this.contribution.removePaymentSource(body).subscribe(async (res: any) => {
                // this.ngOnInit();
                this.getCardDetails();
                this.savedSources.splice(i, 1);
                this.loader.stopLoading();
            }, (err: any) => {
                let message = err.error.message;
                if (message === 'Source not found') {
                    this.toast.presentToastWithOptions(message, (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
                } else {
                    this.toast.presentToastWithOptions(message, (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
                }
                this.loader.stopLoading();
            });
        });
    }

    // to display last 4 digits of card number in card
    saveLast4Digits() {
        if (this.cardNumber.length > 12) {
            let index;
            index = this.cardNumber.length - 12;
            if (this.last4digits) {
                this.last4digits = this.cardNumber.substr(this.cardNumber.length - index);
            } else {
                this.last4digits = this.cardNumber.substr(this.cardNumber.length - index);
            }
        } else {
            this.last4digits = 0;
        }
    }

    // to display last 4 digits of account number in bank card
    saveLast4BankDigits() {
        if (this.accountNumber.length > 12 && this.help_code_epic.bank_type != 'eft') {
            let index;
            index = this.accountNumber.length - 12;
            if (this.last4bankDigits) {
                this.last4bankDigits = this.accountNumber.substr(this.accountNumber.length - index);
            } else {
                this.last4bankDigits = this.accountNumber.substr(this.accountNumber.length - index);
            }
        } else if(this.help_code_epic.bank_type === 'eft') {
            let index;
            index = this.accountNumber.length - 2;
            if (this.last4bankDigits) {
              this.last4bankDigits = this.accountNumber.substr(this.accountNumber.length - index);
            } else {
              this.last4bankDigits = this.accountNumber.substr(this.accountNumber.length - index);
            }
        }
    }

    gotoLogin(){
        this.going_to_login = true;
        let login_url = '/login';
        this.router.navigate([login_url, {
            page: this.page_href,
            color: this.giving_theme ? this.giving_theme.primary_color : "",
            headerFooter: this.show_footer ? "internal" : "external",
            mode: "giving",
            app_style : this.giving_theme ? this.giving_theme.app_style : ""
        }]);
    }

    logOut(){
        this.loader.presentLoading().then(() => {
            this.Auth.logoutUserGiving();
            this.ngOnInit();
            this.user = '';
            this.first_name = '';
            this.last_name = '';
            this.last4digits = '';
            this.accountType = '';
            this.postal_code = '';
            this.routingNumber = '';
            this.accountNumber = '';
            this.cardNumber = '';
            this.exp = '';
            this.cvv = '';
            this.event_provider.hideheader("","");
            this.event_provider.showheaderwithoutmenu(false);
            this.guest = true;
            // this.getUser();
            this.loader.stopLoading();
        });
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data
        if(this.platform.is('android') || this.platform.is('iphone')) {
            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }
        }

        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.user ? this.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.ch_id,
            latitude: this.theme ? this.theme.latitude : "",
            longitude: this.theme ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }

    // in external safari/chrome
    openurlinbrowser(url){
        this.iab.create(url, '_system');
    }

    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
        // setting start time for analytics
        this.start_time_analytics = moment();

        this.currentYear = moment().year();
        this.maxYear = this.currentYear + 10;

        // getting user from local storage
        this.storage.get('user_giving').then(val => {
            if (val != null && val != "guest") {
                this.user = val.user;
                this.token = val.token;
                this.auth = val.auth;
                this.guest = false;
                this.hideMenuFromHeaderAndSidebar();
                this.getCardDetails();
            }
        });
        this.clearAllFields();

    }ionViewWillEnter() {
        this.ngOnInit();
      
        this.event_provider.getChurchData.subscribe(async (res: any) => {
            if (!(Object.keys(res).length === 0)) {
                // asking for password
                let app_pages = res.apppages.find(x => x.name === 'Campaign Registration');
                if (app_pages.password_protection_toggle === 'true') {
                    this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
                }
            }
        });
      
      }

    // To set logo and color to default church theme
    ionViewWillLeave(){
        if (!this.going_to_login){
            this.event_provider.setdefaultcolormode();               // calling event
            this.event_provider.defaultlogo();               // calling event
        }

        if (!this.guest){
            this.Auth.logoutUser();
            this.event_provider.userlogout();               // calling event
            this.event_provider.refresh_home();               // calling event
            this.logoService.setStatus(false);
        }
    }

